document.addEventListener('DOMContentLoaded', () => {
  const sliderNews = document.querySelector('.latest-news');
  const wrapper = sliderNews.querySelector('.latest-news__wrapper');
  const slidesNews = Array.from(sliderNews.querySelectorAll('.news-item'));
  const prevButtonNews = sliderNews.querySelector('.latest-news__arrow--left');
  const nextButtonNews = sliderNews.querySelector('.latest-news__arrow--right');
  const indicatorsNewsContainer = sliderNews.querySelector('.latest-news__indicators');

  let currentIndexNews = 0;
  let slidesToShow = window.innerWidth > 1174 ? 2 : 1; // Кількість показаних слайдів
  let slidesToMove = window.innerWidth > 1174 ? 2 : 1; // Крок руху
  const totalSlidesNews = slidesNews.length;
  let totalIndicatorsNews = slidesToShow === 1 ? totalSlidesNews : Math.ceil(totalSlidesNews / slidesToMove);
  let slideWidth = 0;
  let gap = 0;
  let indicators = [];

  /**
   * Функція для створення індикаторів динамічно
   */
  function createIndicatorsNews() {
    indicatorsNewsContainer.innerHTML = ''; // Очищення існуючих індикаторів
    for (let i = 0; i < totalIndicatorsNews; i++) {
      const indicatorNews = document.createElement('div');
      indicatorNews.classList.add('indicator');
      if (i === currentIndexNews) indicatorNews.classList.add('active');
      indicatorNews.dataset.index = i;
      indicatorsNewsContainer.appendChild(indicatorNews);
    }
    indicators = Array.from(indicatorsNewsContainer.querySelectorAll('.indicator'));
    indicators.forEach(ind => {
      ind.addEventListener('click', () => {
        currentIndexNews = parseInt(ind.dataset.index);
        moveSliderNews();
      });
    });
  }

  /**
   * Функція для оновлення налаштувань слайдера залежно від розміру вікна
   */
  function updateSliderNewsSettings() {
    // Зберігаємо попередні значення
    const previousSlidesToShow = slidesToShow;
    const previousSlidesToMove = slidesToMove;

    // Оновлюємо slidesToShow та slidesToMove базуючись на ширині вікна
    slidesToShow = window.innerWidth > 1174 ? 2 : 1;
    slidesToMove = window.innerWidth > 1174 ? 2 : 1;

    // Перераховуємо кількість індикаторів
    totalIndicatorsNews = slidesToShow === 1 ? totalSlidesNews : Math.ceil(totalSlidesNews / slidesToMove);

    // Коригуємо поточний індекс, якщо він виходить за межі
    if (currentIndexNews >= totalIndicatorsNews) {
      currentIndexNews = totalIndicatorsNews - 1;
    }

    // Завжди викликаємо createIndicatorsNews() для оновлення індикаторів
    createIndicatorsNews();

    // Отримуємо розміри слайдів
    const wrapperStyles = getComputedStyle(wrapper);
    gap = parseInt(wrapperStyles.gap) || 0;
    if (slidesNews.length > 0) {
      slideWidth = slidesNews[0].offsetWidth;
    }

    // Переміщуємо слайдер
    moveSliderNews();
  }

  /**
   * Функція для переміщення слайдера
   */
  function moveSliderNews() {
    const totalShift = currentIndexNews * slidesToMove * (slideWidth + gap);
    wrapper.style.transform = `translateX(-${totalShift}px)`;

    // Оновити активний індикатор
    indicators.forEach(ind => ind.classList.remove('active'));
    if (indicators[currentIndexNews]) {
      indicators[currentIndexNews].classList.add('active');
    }
  }

  /**
   * Обробник для кнопки "Previous"
   */
  prevButtonNews.addEventListener('click', () => {
    currentIndexNews = (currentIndexNews - 1 + totalIndicatorsNews) % totalIndicatorsNews;
    moveSliderNews();
  });

  /**
   * Обробник для кнопки "Next"
   */
  nextButtonNews.addEventListener('click', () => {
    currentIndexNews = (currentIndexNews + 1) % totalIndicatorsNews;
    moveSliderNews();
  });

  /**
   * Обробник для зміни розміру вікна
   */
  window.addEventListener('resize', () => {
    updateSliderNewsSettings();
  });

  // Ініціалізація налаштувань слайдера при завантаженні сторінки
  updateSliderNewsSettings();
});
