document.addEventListener('DOMContentLoaded', () => {
    const colorItems = document.querySelectorAll('.filter-color__color-item');
    const expandButton = document.querySelector('.filter-color__expand-btn');
    const expandButtonText = expandButton.querySelector('.expand-btn-text');

    // Функція для оновлення стану фільтрів залежно від ширини вікна
    const updateFilterState = () => {
        const windowWidth = window.innerWidth;

        if (windowWidth > 900) {
            // Для великих екранів: показати всі елементи і обернути стрілку
            colorItems.forEach(item => item.classList.remove('hidden'));
            expandButton.style.display = 'none'; // Приховати кнопку, якщо не потрібна
            // Якщо ви хочете залишити кнопку з оберненою стрілкою:
            // expandButton.classList.add('active');
            // expandButtonText.textContent = 'Згорнути'; // або інший відповідний текст
        } else {
            // Для малих екранів: обробка показу/приховування елементів
            const hiddenItems = document.querySelectorAll('.filter-color__color-item.hidden');
            if (colorItems.length > 6) {
                colorItems.forEach((item, index) => {
                    if (index >= 6 && !item.classList.contains('hidden') && !expandButton.classList.contains('active')) {
                        item.classList.add('hidden');
                    }
                });

                expandButton.style.display = 'flex'; // Показати кнопку

                // Якщо кнопка ще не була активована, оновити текст
                if (!expandButton.classList.contains('active')) {
                    expandButtonText.textContent = `Ще ${colorItems.length - 6}`;
                }
            } else {
                // Якщо елементів менше або рівно 6, ховаємо кнопку
                expandButton.style.display = 'none';
            }
        }
    };

    // Виклик функції при завантаженні сторінки
    updateFilterState();

    // Виклик функції при зміні розміру вікна
    window.addEventListener('resize', updateFilterState);

    // Обробник події для кнопки розгортання/згортання
    expandButton.addEventListener('click', () => {
        const windowWidth = window.innerWidth;

        if (windowWidth <= 900) {
            const hiddenItems = document.querySelectorAll('.filter-color__color-item.hidden');
            const isHidden = hiddenItems.length > 0;

            if (isHidden) {
                hiddenItems.forEach((item) => item.classList.remove('hidden'));
                expandButtonText.textContent = 'Згорнути';
                expandButton.classList.add('active'); // Додаємо клас active
            } else {
                colorItems.forEach((item, index) => {
                    if (index >= 6) {
                        item.classList.add('hidden');
                    }
                });
                expandButtonText.textContent = `Ще ${colorItems.length - 6}`;
                expandButton.classList.remove('active'); // Видаляємо клас active
            }
        }
    });
});
