document.addEventListener('DOMContentLoaded', function () {
    const searchIcon = document.querySelector('.header__search-icon');
    const searchField = document.querySelector('.header__search-field');
    const searchInput = document.querySelector('.header__search-input');
    const searchResults = document.querySelector('.header__search-results');
    const header = document.querySelector('.header');
    const iconsParent = document.querySelector('.header__icons');
    const originalParent = iconsParent; // Зберігаємо початковий батьківський елемент

    // Media query для max-width 1000px
    const mediaQuery = window.matchMedia('(max-width: 1024px)');

    function handleMediaQueryChange(e) {
        if (e.matches) {
            // Якщо ширина вікна менша або дорівнює 1000px
            if (searchField.parentElement !== header) {
                header.appendChild(searchField);
            }
        } else {
            // Якщо ширина вікна більше 1000px
            if (searchField.parentElement !== originalParent) {
                // Вставляємо searchField на перше місце в originalParent
                originalParent.insertBefore(searchField, originalParent.firstChild);
            }
        }
    }

    // Початковий запуск функції
    handleMediaQueryChange(mediaQuery);
    // Слухач для змін розміру вікна
    mediaQuery.addListener(handleMediaQueryChange);

    // Переключення видимості поля пошуку при кліку на іконку
    searchIcon.addEventListener('click', function (e) {
        e.stopPropagation(); // Запобігаємо спливанню події
        searchField.classList.toggle('active'); // Показуємо/ховаємо поле вводу
        searchField.classList.toggle('hidden');
        searchInput.focus(); // Фокус на поле вводу при відкритті
    });

    // Показуємо результати пошуку при введенні тексту
    searchInput.addEventListener('input', function () {
        if (searchInput.value.trim() !== '') {
            searchResults.style.display = 'block'; // Показуємо результати
        } else {
            searchResults.style.display = 'none'; // Ховаємо результати
        }
    });

    // Ховаємо результати пошуку при втраті фокусу
    searchInput.addEventListener('blur', function () {
        // Невелика затримка для можливості клікнути на результати
        setTimeout(() => {
            searchResults.style.display = 'none'; // Ховаємо результати
        }, 200);
    });

    // Обробка кліку на елементи результатів пошуку
    const searchItems = document.querySelectorAll('.header__search-result-item');
    searchItems.forEach(item => {
        item.addEventListener('click', function () {
            searchInput.value = this.textContent; // Заповнюємо поле вибраним варіантом
            searchResults.style.display = 'none'; // Ховаємо результати
        });
    });

    // Ховаємо поле пошуку при кліку поза ним
    document.addEventListener('click', function (e) {
        if (!searchField.contains(e.target) && !searchIcon.contains(e.target)) {
            searchField.classList.remove('active'); // Ховаємо поле вводу
            searchField.classList.add('hidden');
            searchResults.style.display = 'none'; // Ховаємо результати
        }
    });
});
