// Отримуємо посилання на елементи меню та картки товарів
const menuItems = document.querySelectorAll('.menu__item');
const productCards = document.querySelectorAll('.product-cards');
const burger = document.querySelector('.header__burger');
const burgerContent = document.querySelector('.header__burger-content');

// Функція для активації певної категорії
function activateCategory(item) {
  // Видаляємо клас 'active' з усіх пунктів меню
  menuItems.forEach(menu => menu.classList.remove('active'));
  // Додаємо клас 'active' до вибраного пункту меню
  item.classList.add('active');

  // Приховуємо всі групи карток товарів
  productCards.forEach(card => card.classList.remove('active'));

  // Отримуємо категорію з атрибуту 'data-category' вибраного пункту меню
  const category = item.getAttribute('data-category');
  // Знаходимо відповідну групу карток товарів та робимо її активною
  const selectedProductCards = document.querySelector(`.product-cards[data-category="${category}"]`);
  if (selectedProductCards) {
    selectedProductCards.classList.add('active');
  }
}

// Обробник кліку на бургер меню
burger.addEventListener('click', function() {
  // Перемикаємо клас 'active' для бургера та контенту
  this.classList.toggle('active');
  burgerContent.classList.toggle('active');

  // Якщо бургер зараз активний (відкритий), активуємо першу категорію
  if (this.classList.contains('active')) {
    const firstMenuItem = menuItems[0];
    if (firstMenuItem) {
      activateCategory(firstMenuItem);
    }
  }
});

// Обробники кліку для кожного пункту меню
menuItems.forEach(item => {
  item.addEventListener('click', function() {
    activateCategory(item);
  });
});
