document.addEventListener('DOMContentLoaded', () => {
    // Select the main slider container
    const slider = document.querySelector('.top-sales');
    
    // Select the wrapper that contains all slides
    const wrapper = slider.querySelector('.top-sales__wrapper');
    
    // Convert NodeList of slides to an array for easier manipulation
    const slides = Array.from(slider.querySelectorAll('.top-sales__item'));
    
    // Select navigation buttons
    const prevButton = slider.querySelector('.top-sales__arrow--left');
    const nextButton = slider.querySelector('.top-sales__arrow--right');
    
    // Select the indicators container
    const indicatorsContainer = slider.querySelector('.top-sales__indicators');
  
    // Initialize the current index to the first slide
    let currentIndex = 0;
    
    // Number of slides to show based on window width
    let slidesToShow = window.innerWidth > 1174 ? 3 : 1;
    
    // Total number of slides
    const totalSlides = slides.length;
    
    // Total number of indicators based on slidesToShow
    let totalIndicators = slidesToShow === 1 ? totalSlides : totalSlides - slidesToShow + 1;
    
    // Variables to store slide width and gap in pixels


    
    // Function to create indicators dynamically
    function createIndicators() {
      // Clear existing indicators
      indicatorsContainer.innerHTML = '';
      
      // Create necessary number of indicators
      for (let i = 0; i < totalIndicators; i++) {
        const indicator = document.createElement('div');
        indicator.classList.add('indicator');
        
        // Set the first indicator as active by default
        if (i === currentIndex) indicator.classList.add('active');
        
        // Assign a data attribute to track the slide index
        indicator.dataset.index = i;
        
        // Append the indicator to the container
        indicatorsContainer.appendChild(indicator);
      }
    }
  
    // Initial creation of indicators
    createIndicators();
  
    // Convert NodeList of indicators to an array for easier manipulation
    let indicators = Array.from(indicatorsContainer.querySelectorAll('.indicator'));
  
    /**
     * Function to update slider settings based on window size
     * Adjusts slidesToShow, recalculates totalIndicators, and updates slide dimensions
     */
    function updateSliderSettings() {
      // Update slidesToShow based on current window width
      slidesToShow = window.innerWidth > 1174 ? 3 : 1;
      
      // Recalculate total indicators
      totalIndicators = slidesToShow === 1 ? totalSlides : totalSlides - slidesToShow + 1;
      
      // Ensure totalIndicators is at least 1 to avoid negative values
      if (totalIndicators < 1) totalIndicators = 1;
      
      // Adjust currentIndex if it exceeds the new totalIndicators
      if (currentIndex > totalIndicators - 1) {
        currentIndex = totalIndicators - 1;
      }
      
      // Recreate indicators if the number has changed
      if (indicators.length !== totalIndicators) {
        createIndicators();
        
        // Update the indicators array after recreating them
        indicators = Array.from(indicatorsContainer.querySelectorAll('.indicator'));
        
        // Add click event listeners to new indicators
        indicators.forEach(ind => {
          ind.addEventListener('click', () => {
            // Update currentIndex based on the clicked indicator
            currentIndex = parseInt(ind.dataset.index);
            
            // Move the slider to the new position
            moveSlider();
          });
        });
      }
      
      // Get the computed styles of the wrapper to retrieve the gap
      const wrapperStyles = getComputedStyle(wrapper);
      gap = parseInt(wrapperStyles.gap);
      
      // Get the width of a single slide (including padding and border)
      if (slides.length > 0) {
        slideWidth = slides[0].offsetWidth;
      }
      
      // Move the slider to the correct position based on the updated settings
      moveSlider();
    }
  
    /**
     * Function to move the slider based on the currentIndex
     * Calculates the total shift in pixels and applies the transform
     * Updates the active indicator
     */
    function moveSlider() {
      // Calculate the total shift: slide width multiplied by currentIndex plus gaps
      const totalShift = currentIndex * (slideWidth + gap);
      
      // Apply the CSS transform to shift the wrapper
      wrapper.style.transform = `translateX(-${totalShift}px)`;
      
      // Remove the 'active' class from all indicators
      indicators.forEach(ind => ind.classList.remove('active'));
      
      // Add the 'active' class to the current indicator
      if (indicators[currentIndex]) {
        indicators[currentIndex].classList.add('active');
      }
    }
  
    /**
     * Event handler for the "Previous" button
     * Decrements the currentIndex and moves the slider
     */
    prevButton.addEventListener('click', () => {
      // Decrement currentIndex; wrap around if necessary
      currentIndex = (currentIndex - 1 + totalIndicators) % totalIndicators;
      
      // Move the slider to the new position
      moveSlider();
    });
  
    /**
     * Event handler for the "Next" button
     * Increments the currentIndex and moves the slider
     */
    nextButton.addEventListener('click', () => {
      // Increment currentIndex; wrap around if necessary
      currentIndex = (currentIndex + 1) % totalIndicators;
      
      // Move the slider to the new position
      moveSlider();
    });
  
    /**
     * Event handler for window resize
     * Updates the slider settings to adapt to the new window size
     */
    window.addEventListener('resize', () => {
      updateSliderSettings();
    });
  
    // Initialize slider settings when the page loads
    updateSliderSettings();
});
